.catalog-group{
  padding: 1.25em;
  border: 1px solid $dividerGray;
  &__top{
    display: grid;
    grid-template-columns: 13.375em 13.125em calc(100% - 48.93em) 10em 2.5em 4.93em;
    align-items: flex-start;
    gap: 1em;
  }
  .badges,
  .badges2{
    position: static;
  }
  .catalog-card__info{
    text-align: left;
    margin: 0;
    gap: .125em;
  }
  .catalog-card__text{
    margin: 0;
  }
  .product-props{
    margin: 0;
  }
  .catalog-card__logo{
    position: static;
    width: 100%;
    transform: unset;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 1em;
    border-top: 1px solid $dividerGray;
    &-right{
      display: flex;
      align-items: center;
    }
    .catalog-card__btn{
      margin: 0 0 0 1.625em;
      height: 2em;
    }
  }
  &__hint{
    margin-right: auto;
    max-width: 20em;
    font-weight: 600;
    .text10{
      &:before{
        content: '*';
        margin-right: .25em;
      }
      div{
        line-height: inherit;
      }
    }
  }
  &__total{
    .text12{
      margin-right: .25em;
      display: inline-block;
    }
  }
}

.catalog-group2{
  .catalog-group__top{
    grid-template-columns: 13.375em 9.375em 2.75em calc(100% - 48.93em) 10em 2.5em 4.93em;
  }
  .favourite-btn{
    border: none;
    margin-top: -.25em;
  }
}


.catalog-table{
  margin-top: .67em;
  table{
    border-collapse: collapse;
    width: 100%;
  }
  th,
  td{
    font-size: 1em;
    padding-left: 1em;
    &:nth-child(1){
      min-width: 8em;
      width: 8em;
    }
    &:nth-child(2){
      min-width: 5.375em;
      width: 5.375em;
    }
    &:nth-child(3){
      min-width: 19.625em;
      width: 19.625em;
    }
  }
  th{
    border-top: 1px solid $dividerGray;
    padding-top: .5em;
    padding-bottom: .25em;
    text-align: left;
    span{
      font-size: .625em;
      line-height: 180%;
      letter-spacing: .025em;
    }
    &:nth-child(1){
      padding-left: 2.19em;
    }
  }
  td{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    border-top: 1px solid $dividerGray;
    vertical-align: top;
  }
  &__media{
    max-width: 5em;
    height: 3.25em;
    width: 100%;
    overflow: hidden;
  }
  &__img{
    margin-top: -1.75em;
    @include imgCover(100%);
  }
  &__models{
    &-item{
      display: block;
      font-size: .75em;
      line-height: 217%;
      letter-spacing: .025em;
      font-weight: 600;
    }
  }
  .catalog-card__nav{
    margin: 0;
  }
  .text12{
    letter-spacing: unset;
  }
  &._type2{
    th,
    td{
      &:nth-child(3){
        width: 11.5em;
        min-width: 11.5em;
      }
      &:nth-child(4){
        max-width: 11.5em;
      }
      &:nth-child(5),
      &:nth-child(6),
      &:nth-child(7),
      &:nth-child(8),
      &:nth-child(9)
      {
        text-align: center;
      }
    }
    td{
      vertical-align: middle;
    }
  }
  &._type3{
    th,td{
      &:nth-child(3){
        width: 20.5em;
        min-width: 20.5em;
      }
      &:nth-child(4){
        padding-left: 0;
        width: calc(100% - 24.875em);
      }
    }
    td{
      vertical-align: middle;
    }
    .cartcalc{
      margin: 0 auto;
    }
  }
}

.pair-head,
.pair-row{
  display: grid;
  grid-template-columns: repeat(4,25%);
  &__col{
    padding-left: 1em;
    text-align: center;
  }
}

.pair-row{
  @include lastM(0 0 .45em);
}
