.news-page{
  padding-bottom: 3.25em;
  &__grid{
    padding: 0 1.25em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (5.62em * 3 / 4)));
    gap: 1.25em 5.62em;
    margin-top: 1.875em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.25em;
  }
  .pagination{
    padding: 0 1.25em;
  }
}

.page-title{
  padding: .625em;
  @include grid100gap(.625em);
}

.news-item{
  display: grid;
  grid-template-columns: 35.5em calc(100% - 41.125em);
  gap: 5.625em;
  padding: 1.25em 0;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__date{
    margin-bottom: .25em;
  }
  &__text{
    margin-top: .89em;
    &:first-child{
      margin-top: 0;
    }
    p{
      line-height: inherit;
    }
  }
}
