.blog{
  padding-bottom: 5.3em;
  &__items{
    margin: 2.94em auto 0;
    width: 100%;
    max-width: 68em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 6.75em));
    gap: 3.75em 13.5em;
  }
  .pagination{
    width: 100%;
    max-width: 68em;
    margin-left: auto;
    margin-right: auto;
    padding-left: 3em;
  }
}

.blog-nav{
  margin-top: 2.5em;
  padding-bottom: 1.5em;
  position: relative;
  @include center;
  &:after{
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: 45.75em;
    max-width: 100%;
    height: 1px;
    background-color: $dividerGray;
  }
  &__link{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 600;
    &:not(:last-child){
      &:after{
        content: '';
        flex-shrink: 0;
        width: 2px;
        height: 1em;
        background-color: #E2E2E2;
        margin: 0 1em;
      }
    }
    span{
      font-size: 1.25em;
      line-height: 150%;
      letter-spacing: 0.05625em;
    }
  }
}

.blog-slider{
  overflow: hidden;
  width: 43.75em;
  max-width: 100%;
  margin: 2.25em auto 0;
  .swiper{
    &-pagination{
      position: static;
      margin-top: .69em;
      &-bullet{
        width: 0.625em;
        height: 0.625em;
        background: #D6D6D6;
        border-radius: 50%;
        border: none;
        transform: unset;
        &-active{
          background: #869791;
        }
      }
    }
  }
}

.blog-card{
  &__img{
    @include imgCover(63.6%);
  }
  &__type{
    margin-top: .94em;
    text-align: center;
    text-transform: uppercase;
  }
  &__info{
    @include grid100gap(.56em);
    text-align: center;
    margin-top: 1.44em;
    .text24{
      text-transform: uppercase;
    }
  }
  &__desc{
    margin-top: 1.25em;
    text-align: center;
    line-height: 187%;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: .55em;
  }
  &__tag{
    margin: .19em 1.5em 0 0;
    color: #8E8E8E;
    span{
      font-size: .875em;
      line-height: 191%;
      letter-spacing: 0.05625em;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
}

.blog-item{
  .blog-nav{
    margin-top: 1em;
  }
  &__inner{
    margin: 0 auto;
    width: 69.375em;
    max-width: 100%;
  }
  &__top{
    margin-top: 1.25em;
    .text48{
      text-align: center;
    }
    .text{
      margin-top: 1.69em;
    }
  }
  .images__item{
    margin: 1.81em 0;
  }
  &__quote{
    margin: 2.63em 0 0;
    background: #EDEDED;
    padding: 1.4em 5.2em;
    border-left: .5em solid $primary;
  }
  &__p{
    margin: 1.33em 0 0;
  }
  .images{
    margin-top: 1.69em;
  }
  &__products{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .125em));
    gap: .85em .25em;
    width: 62.125em;
    max-width: 100%;
    margin: 0 auto;
  }
}

.blog-product{
  text-align: center;
  display: block;
  &__img{
    height: 10.9375em;
    margin-bottom: .25em;
    @include center;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__title{
    font-size: 0.8125em;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
}

.share{
  margin-top: 3.83em;
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .25em;
    &:hover{
      img{
        transform: translateY(-25%);
      }
    }
    img{
      width: 100%;
      transition: all .4s;
    }
  }
}

.news-card2{
  .text24{
    text-transform: uppercase;
  }
}
