@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  &-bullet{
    opacity: 1;
    width: .875em;
    height: .625em;
    border: 1px solid $primary;
    background-color: transparent;
    transform: skew(-20deg, 0);
    border-radius: 0;
    &-active{
      background-color: $primary;
    }
  }
  &._white{
    .swiper-pagination-bullet{
      border-color: #fff;
      &:hover{
        border-color: $primary;
      }
      &-active{
        background-color: #fff;
        pointer-events: none;
      }
    }
  }
}

.swiper-button{
  width: 2.875em;
  height: 2.875em;
  img{
    width: 100%;
    display: block;
  }
}

.info-top{
  @include grid100gap(3.12em);
  text-align: center;
  max-width: 70em;
  margin: 0 auto;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.2);
    }
  }
  img{
    transition: all 1.5s;
  }
}

.slider{
  margin-top: 3.125em;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 3.125em;
    .swiper{
      &-pagination{
        position: static;
        width: unset;
      }
      &-nav{
        display: flex;
        align-items: center;
      }
      &-button{
        position: static;
        margin: 0 0 0 .625em;
      }
    }
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev{
        left: -3em;
      }
      &-next{
        right: -3em;
      }
    }
  }
}

.load-more{
  display: flex;
  justify-content: center;
  margin-top: 1.25em;
}

.pagination{
  display: flex;
  align-items: center;
  margin-top: 1.25em;
  &__items{
    display: flex;
    align-items: center;
  }
  &__btn{
    flex-shrink: 0;
    width: 1.25em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
    &._disabled{
      pointer-events: none;
      path{
        stroke: $bgGray;
      }
    }
  }
  &__item{
    font-weight: 700;
    margin: 0 .25em;
    &:hover,
    &._active{
      color: $primary;
    }
  }
}
