.contacts-page{
  padding-bottom: 3.125em;
  &__nav{
    margin-top: 1.875em;
  }
}

.tabs-slider{
  overflow: hidden;
  display: flex;
  .swiper{
    &-slide{
      width: auto;
    }
    &-button{
      margin: 0;
      position: static;
      &-next{
        margin-left: .625em;
      }
    }
    &-nav{
      position: relative;
      z-index: 2;
      background-color: #fff;
      display: flex;
      flex-shrink: 0;
    }
  }
}

.tab{
  padding: 0 1.25em;
  height: 3.125em;
  display: flex;
  align-items: center;
  font-weight: 600;
  border-bottom: 2px solid transparent;
  span{
    font-size: 1.25em;
    line-height: 150%;
    letter-spacing: 0.05625em;
  }
  &._active{
    border-color: $primary;
  }
}

.map{
  height: 30em;
  margin-top: 1.25em;
}

.contacts-info{
  margin-top: 1.25em;
  @include grid100gap(1.875em);
  .text17{
    max-width: 45em;
  }
}

.contacts-phones{
  margin-top: 1.56em;
  .text18{
    line-height: 177%;
  }
  &__items{
    margin-top: 1.875em;
    a{
      font-weight: 700;
    }
  }
}
