.auth{
  padding-bottom: 3.75em;
}

.auth-form{
  margin-top: 1.25em;
  &__nav{
    display: flex;
    justify-content: center;
    padding-top: .5em;
  }
}

.form-item,
.form-items{
  padding: 1.25em 0;
  border-bottom: 1px solid #E4E7EC;
  &:last-child{
    padding-bottom: 0;
    border: none;
  }
}

.form-items{
  @include grid100gap(1.5em);
  .form-item{
    padding: 0;
    border: none;
  }
}

.form-item{
  display: grid;
  grid-template-columns: 17.5em calc(100% - 19.5em);
  gap: 2em;
  &__title{
    padding: .625em 0;
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
    max-width: 45em;
    width: 100%;
    ._col2{
      grid-column: span 2;
    }
    ._col3{
      grid-column: 1/-1;
    }
  }
  &__links{
    display: flex;
    align-items: center;
    padding: .625em 0;
  }
  &__link{
    color: $primary;
    margin-right: 2em;
    &:hover{
      color: $black;
    }
  }
  &__btn{
    border-radius: 0.25em;
    padding: 0 1em;
    height: 2.5em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
}
