.fg {
  position: relative;
  margin: 0;
  textarea,
  input,
  .nice-select{
    border: 2px solid #D0D5DD;
    width: 100%;
    background-color: transparent;
    color: $black;
  }
  textarea,
  input {
    padding: 0 .78em;
    height: 2.22em;
    font-size: 1.125em;
    line-height: 150%;
    border-radius: .44em;
    &::placeholder {
      color: $textLightGray;
    }
    &:focus{
      border-color: $primary;
    }
    &:disabled{
      background-color: #F9FAFB;
      box-shadow: 0 .0625em .125em 0 rgba(16, 24, 40, 0.05);
      pointer-events: none;
    }
  }
  textarea{
    padding-top: .5em;
    padding-bottom: .5em;
    height: 7.12em;
    resize: none;
  }
  .nice-select{
    font-size: 1em;
    height: 2.5em;
    padding: 0 3em 0 .875em;
    background-image: url(../img/select-arrow.svg) !important;
    background-repeat: no-repeat !important;
    background-position: right .875em center !important;
    background-size: 1.125em auto !important;
    .current{
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      font-size: 1.125em;
    }
  }
  .list{
    //margin-top: 0;
    background: #fff;
    box-shadow: 0 1em 1em 0 rgba(0, 0, 0, 0.10), 0 .5em .5em 0 rgba(0, 0, 0, 0.05);
    border: none;
    padding: 1.5em 1em;
    @include grid100gap(1em);
    max-height: 18em;
    overflow-y: auto;
  }
  .option{
    border: none;
    background-color: transparent;
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.05625em;
    padding: 0;
    &:hover{
      color: $primary;
    }
  }
  .option:hover,
  .option.focus,
  .option.selected.focus{
    background-color: transparent;
    font-weight: 400;
  }
  .option.selected{
    background-image: url(../img/check3.svg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 1em auto;
  }
}

.pass-toggle{
  position: absolute;
  width: 1em;
  height: 1em;
  right: .875em;
  top: 50%;
  transform: translateY(-50%);
  svg{
    @include centerAbsolute;
    width: 100%;
    height: auto;
  }
  ._view1{
    opacity: 0;
  }
  &._active{
    ._view2{
      opacity: 0;
    }
    ._view1{
      opacity: 1;
    }
  }
}

.upload,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 3;
    cursor: pointer;
  }
}

.m-check {
  input {
    &:checked~label {
      &::after {
        opacity: 1;
      }
    }
  }
  label {
    padding-left: 2em;
    position: relative;
    min-height: 1.5em;
    cursor: pointer;
    display: block;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 1.5em;
      height: 1.5em;
      left: 0;
      top: 0;
    }
    &:before {
      border-radius: .25em;
      border: 2px solid $dividerGray;
      transition: all .100s;
    }
    &:after{
      z-index: 2;
      background-image: url(../img/check.svg);
      @include bgImgSize(1.125em);
      opacity: 0;
    }
  }
  span{
    font-size: 0.875em;
    line-height: 172%;
    letter-spacing: 0.05625em;
  }
  a{
    color: $primary;
  }
  &._black{
    label:after{
      background-image: url(../img/check4.svg);
    }
  }
}

.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.5em;
    margin-right: .5em;
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: $textLightGray;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $primary;
      }
      &~.rating-input__item {
        path{
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $primary;
  }
}

.upload{
  input:hover~.upload__inner{
    border-color: $primary;
  }
  &__inner{
    transition: all .3s;
    padding: 1em 1.5em;
    border-radius: 0.5em;
    border: 1px dashed #E4E7EC;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__icon{
    width: 2.5em;
    height: 2.5em;
    margin: .25em 0 .75em;
    @include center;
    border-radius: 50%;
    border: .375em solid #F9FAFB;
    background: #F2F4F7;
    img{
      width: 1.25em;
    }
  }
  .text14{
    line-height: 143%;
    &:not(:first-child){
      margin-top: .285em;
    }
  }
}
