.content-block{
  padding: 3em 0;
  .info-top{
    &:not(:last-child){
      margin-bottom: 3em;
    }
  }
  .honor__grid,
  .holding__grid{
    margin-top: 0;
  }
  &__products{
    width: 72em;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    .product-card{
      border: none;
      padding: 0 4.5em 1.875em;
    }
  }
}

.grid-column2,
.grid-column3,
.grid-column4{
  display: grid;
  gap: 3em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - 1.5em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 2em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 9em / 4));
}

.best-card{
  &__img{
    @include imgCover(80%);
  }
  &__content{
    margin-top: .625em;
    text-align: center;
  }
}

.product-card{
  padding: 0 1.875em .5em;
  border: 1px solid $dividerGray;
  overflow: hidden;
  &__img{
    padding-bottom: 100%;
    position: relative;
    &:hover{
      img{
        transform: scale(1.2) translate(-45%,-50%);
      }
    }
    img{
      transition: all 1.5s;
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: 1.25em;
    text-align: center;
    @include grid100gap(.25em);
    .text18{
      line-height: 133%;
    }
  }
}

.product-props{
  margin-top: 1.25em;
  @include center;
  &__item{
    margin: 0 .75em;
    width: 1.5em;
    position: relative;
    &:hover{
      cursor: pointer;
      .hint-block{
        opacity: 1;
        pointer-events: unset;
      }
    }
    img{
      width: 100%;
      display: block;
    }
    span{
      @include centerAbsolute;
      font-size: .875em;
      line-height: 121%;
      font-weight: 700;
    }
    &._big{
      width: 2em;
    }
    &._right{
      span{
        transform: unset;
        left: unset;
        top: unset;
        right: 0;
        bottom: -.2em;
      }
    }
  }
}

.hint-block{
  position: absolute;
  bottom: 99%;
  left: 50%;
  transform: translateX(-50%);
  padding-bottom: .25em;
  white-space: nowrap;
  z-index: 2;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    padding: .5em 1em;
    border-radius: 0.25em;
    background: #fff;
    box-shadow: 0 .5em 1.25em 0 rgba(0, 51, 153, 0.08);
  }
}

.holding-slider{
  position: relative;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
    &-button{
      top: 7em;
      margin-top: 0;
      &-prev{
        left: 3em;
      }
      &-next{
        right: 3em;
      }
    }
  }
  .holding-card{
    &__img{
      padding-bottom: 56.25%;
    }
    &__content{
      text-align: left;
      padding: .5em 1.875em 0;
      margin: 0;
    }
  }
}

.remark{
  padding: .5em 3.875em;
  background-color: $bgGray;
  width: 69.375em;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  border-left: 4px solid $primary;
  position: relative;
  &__text{
    text-align: center;
  }
  &__by{
    margin-top: 1em;
    @include center;
    &-img{
      width: 2.5em;
      height: 2.5em;
      flex-shrink: 0;
      margin-right: .75em;
      img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
        display: block;
        object-fit: cover;
      }
    }
    &-text{
      letter-spacing: -0.01125em;
      line-height: 144%;
    }
  }
  &__quote{
    position: absolute;
    width: 1.8em;
    display: block;
    &._top{
      top: .94em;
      left: .94em;
    }
    &._bottom{
      right: 1.12em;
      bottom: .81em;
    }
  }
}

.img-slider{
  &__item{
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-pagination{
      position: static;
      margin-top: 1em;
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.info{
  @include grid100gap(6em);
}

.info-block{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.5em));
  gap: 3em;
  @media screen and (min-width: 993px){
    &:nth-child(even){
      .info-block__col:nth-child(2){
        order: -1;
      }
    }
  }
  &__img{
    display: block;
    width: 100%;
  }
}

.guarantee-item,
.company-item,
.mission-item{
  display: flex;
  align-items: center;
  &__img{
    width: 10.75em;
    height: 10.75em;
    flex-shrink: 0;
    margin-right: 2em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 50%;
    }
  }
}

.mission-item{
  flex-direction: column;
  text-align: center;
  &__content{
    margin-top: 2em;
    @include grid100gap(.5em);
  }
}

.images{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
  gap: .5em;
  &._min{
    width: 69.375em;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &._double{
    grid-template-columns: repeat(2,calc(50% - .25em));
  }
  &._single{
    grid-template-columns: 100%;
  }
  &__item{
    img{
      width: 100%;
      display: block;
    }
  }
}

.guarantee-items{
  @include grid100gap(3em);
}

.guarantee-item{
  align-items: flex-start;
  .text{
    margin-top: .5em;
  }
}

.text {
  ul{
    padding-left: .75em;
    li{
      display: flex;
      &:before{
        content: '';
        flex-shrink: 0;
        width: .25em;
        height: .25em;
        border-radius: 50%;
        background-color: $black;
        transform: translateY(.75em);
        margin-right: .625em;
      }
    }
  }
}
