.category-slider{
  margin: 0;
  overflow: hidden;
  .swiper{
    &-slide{
      width: calc(100% / 7);
    }
    &-button{
      width: 1.5em;
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
}

.category-card{
  text-align: center;
  color: #B2B2B2;
  &__img{
    width: 6.25em;
    height: 6.25em;
    margin: 0 auto 1.5em;
    position: relative;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
}

.catalog{
  padding: 2.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: 13.75em calc(100% - 13.75em - 3.125em);
    gap: 3.125em;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 1.875em;
    .pagination{
      margin: 0 0 0 1.875em;
    }
    .pagination2{
      margin-left: auto;
    }
  }
  &__sort{
    margin-left: 1.875em;
    min-width: 13.3125em;
    .nice-select{
      height: 1.5em;
      border-radius: 0;
      padding: 0 2.44em 0 .94em;
      background-image: url(../img/select-arrow2.svg) !important;
      background-size: 1.25em auto !important;
      .current{
        font-size: .875em;
      }
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
  .load-more{
    margin-top: 2.625em;
  }
  &__tables{
    @include grid100gap(1.25em);
    margin-top: 1.875em;
  }
}

.catalog-sidebar{
  &__inner{
    border-right: 1px solid $dividerGray;
    @include grid100gap(1.25em);
    padding-bottom: 1em;
  }
  &__bottom{
    padding: 1.25em;
    background-color: $bgGray;
    @include grid100gap(1em);
  }
  &__buttons{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .0625em));
    gap: .125em;
  }
  &__btn{
    height: 2.5em;
    span{
      font-size: .875em;
    }
  }
}

.filter-group{
  &._active{
    .filter-group__btn img{
      transform: rotate(-180deg);
    }
  }
  &__show{
    padding-right: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 1.25em;
    flex-shrink: 0;
    margin-left: .5em;
    img{
      width: 100%;
      transition: inherit;
    }
  }
  &__hidden{
    margin-top: .625em;
    display: none;
  }
  &__items{
    @include grid100gap(.5em);
  }
  &__item{
    &-show{
      display: flex;
      align-items: center;
      min-height: 2.125em;
      .m-check{
        label{
          &:before,
          &:after{
            top: 50%;
            transform: translateY(-50%);
          }
        }
        span{
          line-height: 143%;
        }
      }
    }
    &-btn{
      border-radius: 0.25em;
      border: 2px solid $dividerGray;
      width: 1em;
      height: 1em;
      flex-shrink: 0;
      position: relative;
      margin-right: .5em;
      img{
        @include centerAbsolute;
        width: .75em;
        transition: inherit;
      }
      ._view2{
        opacity: 0;
      }
    }
    &-hidden{
      display: none;
      padding-left: 3.5em;
    }
    &._active{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__checks{
    @include grid100gap(.5em);
  }
  .m-check{
    ._halfChecked{
      &~label{
        &:after{
          background-image: unset;
          width: 1.125em;
          height: 1.125em;
          border-radius: .25em;
          background-color: $primary;
          left: .1875em;
          opacity: 1;
        }
      }
    }
  }
}

.filter-slider{
  padding: 0 1.25em;
  &__inputs{
    pointer-events: none;
    display: grid;
    grid-template-columns: calc(50% - .5em) 1em calc(50% - .5em);
  }
  &__split{
    text-align: center;
  }
  &__input{
    input{
      width: 100%;
      border: none;
      height: 1.67em;
      font-size: 1.125em;
      letter-spacing: 0.05625em;
    }
    &._right{
      input{
        text-align: right;
      }
    }
  }
}

.range-slider{
  margin-top: .5em;
  margin-left: -1em;
  width: calc(100% + 1em);
  &.noUi-horizontal{
    height: 0.875em;
  }
  &.noUi-target{
    border: none;
    background: #D9D9D9;
    border-radius: .25em;
  }
  .noUi-connect{
    background-color: #ED5564;
  }
  .noUi-handle{
    cursor: pointer;
    border: none;
    background-color: #D94353;
    height: 1.375em;
    width: 2px;
    right: -1px;
    top: -.25em;
    box-shadow: unset;
    &:before {
      display: none;
      //content: attr(aria-valuenow);
    }
    &:after{
      display: block;
      @include centerAbsolute;
      width: 1.375em;
      height: 100%;
      opacity: 0;
    }
  }
}

.catalog-view{
  display: flex;
  align-items: center;
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 .25em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover,
    &._active{
      path{
        stroke: $primary;
      }
    }
  }
}

.catalog-item{
  border-bottom: 1px solid $dividerGray;
  border-right: 1px solid $dividerGray;
  position: relative;
  &:hover{
    z-index: 5;
    ._small{
      opacity: 0;
      pointer-events: none;
    }
    ._detail{
      opacity: 1;
      pointer-events: unset;
    }
  }
  ._small{
    .card-toggler{
      display: block;
      &__item{
        margin-bottom: .3125em;
      }
    }
  }
  ._detail{
    width: auto;
    min-width: calc(100% + 1.25em);
    min-height: calc(100% + 1.25em);
    position: absolute;
    left: 50%;
    top: -.625em;
    transform: translateX(-50%);
    opacity: 0;
    pointer-events: none;
    z-index: 3;
    border-radius: 0.25em;
    border: 1px solid $dividerGray;
    background-color: #fff;
    box-shadow: 0px 16px 16px 0px rgba(0, 0, 0, 0.10), 0px 8px 8px 0px rgba(0, 0, 0, 0.05);
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3){
    border-top: 1px solid $dividerGray;
  }
  &:nth-child(3n - 2){
    border-left: 1px solid $dividerGray;
  }
}

.catalog-card{
  padding: 0 1.5em 1.875em .125em;
  display: flex;
  width: 100%;
  min-height: 100%;
  position: relative;
  transition: all .1s;
  &__info{
    text-align: center;
    margin-top: 1.25em;
    @include grid100gap(.25em);
  }
  &__text{
    margin-top: .5em;
    p{
      font-size: .875em;
      line-height: 129%;
      letter-spacing: 0.05625em;
    }
    b{
      font-weight: 600;
    }
  }
  &__logo{
    position: absolute;
    top: 2.5em;
    left: 50%;
    transform: translateX(-50%);
    width: 4.93em;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
  .product-props{
    margin-top: 1.25em;
  }
  &__sliders{
    height: 15em;
    position: relative;
  }
  &__sidebar{
    position: relative;
    z-index: 2;
    flex-shrink: 0;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
    align-self: center;
  }
  &__btn{
    padding: 0 1em;
    height: 2.75em;
    margin-right: .5em;
    border-radius: .25em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__orders{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__view{
    position: absolute;
    opacity: 0;
    pointer-events: none;
    &._active{
      position: static;
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.card-slider{
  width: 15em;
  height: 15em;
  position: relative;
  &:hover{
    .swiper-pagination{
      opacity: 1;
    }
  }
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__img{
    @include imgCover(100%);
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-pagination{
      position: absolute;
      padding: .94em .84em;
      height: calc(100% + .75em);
      width: 100%;
      left: 0;
      top: 0;
      z-index: 3;
      display: flex;
      transition: all .3s;
      opacity: 0;
      &-bullet{
        flex-grow: 1;
        margin: 0 .25em !important;
        height: 100%;
        border-radius: 0;
        background-color: transparent;
        position: relative;
        border: none;
        transform: unset;
        &:before{
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: .25em;
          border-radius: .25em;
          background: $bgGray;
          transition: all .3s;
        }
        &-active:before{
          background: $dividerGray;
        }
      }
    }
  }
}

.card-toggler{
  padding: 2.3125em 0 0 .3125em;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 19.375em;
  min-width: 3.125em;
  position: relative;
  z-index: 2;
  &__item{
    flex-shrink: 0;
    width: 2.5em;
    border: 1px solid #CECECE;
    border-radius: .25em;
    overflow: hidden;
    transition: all .3s;
    margin: 0 0 .3125em .3125em;
    cursor: pointer;
    &:hover,
    &._active{
      border-color: $primary;
    }
  }
  &__more{
    margin-top: .5em;
    position: relative;
    white-space: nowrap;
    .text12{
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}

.badges{
  position: absolute;
  top: -1px;
  left: -1px;
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  z-index: 2;
}

.badge{
  flex-shrink: 0;
  padding: 0 .81em;
  height: 2em;
  display: flex;
  align-items: center;
  background: #2CC7D6;
  color: #fff;
  &._red{
    background: $primary;
  }
  &._green{
    background: #9AC366;
  }
}

.badges2{
  position: absolute;
  top: 2.5em;
  right: 1.25em;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.badge2{
  width: 2em;
  margin: 0 0 .5em;
  @include center;
  img{
    max-width: 100%;
    max-height: 2em;
    display: block;
  }
}

.favourite-btn{
  width: 2.75em;
  height: 2.75em;
  border-radius: .25em;
  border: 1px solid $dividerGray;
  &:hover{
    border-color: $primary;
  }
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
  &__icon{
    height: 100%;
    width: 100%;
    position: relative;
    display: block;
    svg{
      @include centerAbsolute;
      width: 1.125em;
      height: auto;
    }
    ._view1:hover{
      path{
        stroke: $primary;
      }
    }
    ._view2{
      opacity: 0;
    }
  }
}

.prices{
  margin-top: 1.25em;
  @include center;
  &__item{
    display: flex;
    align-items: center;
    @include lastM(0 .625em 0 0);
    .text20{
      margin-right: .2em;
    }
  }
}

.catalog-order{
  position: relative;
  &._active{
    .catalog-order__price2{
      opacity: 1;
    }
  }
  &__count{
    position: absolute;
    margin-left: .01em;
    left: 100%;
    top: .25em;
    width: 0.75em;
    height: 0.75em;
    border-radius: 50%;
    @include center;
    font-weight: 800;
    color: #fff;
    background-color: $primary;
    span{
      font-size: .5em;
      line-height: 120%;
    }
  }
  &__show{
    cursor: pointer;
    display: grid;
    grid-template-columns: calc(100% - 2em) 1.25em;
    gap: .75em;
  }
  &__btn{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    display: flex;
    justify-content: space-between;
  }
  &__prices{
    display: flex;
    &-num{
      position: relative;
      margin-right: .25em;
    }
  }
  &__price2{
    position: absolute;
    right: 0;
    top: 60%;
    color: $textMediumGray;
    letter-spacing: 0.00788em;
    text-decoration: line-through;
    opacity: 0;
    pointer-events: none;
    transition: all .4s;
  }
  &__item{
    @include lastM(0 1em 0 0);
    white-space: nowrap;
  }
  &__hidden{
    margin-top: 1.125em;
    display: none;
  }
  &__products{
    display: flex;
    align-items: center;
    .cartcalc{
      margin-left: .81em;
    }
  }
}

.cartcalc{
  width: 4.375em;
  height: 1.5em;
  display: grid;
  grid-template-columns: 1.5em 1.375em 1.5em;
  border-radius: .25em;
  border: 1px solid $dividerGray;
  input,
  button{
    width: 100%;
    height: 100%;
    border: none;
    font-size: .75em;
    line-height: 120%;
  }
  button{
    @include center;
  }
  input{
    border-right: 1px solid $dividerGray;
    border-left: 1px solid $dividerGray;
    text-align: center;
    font-weight: 800;
  }
}

.catalog-total{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2em;
  &__info{
    @include grid100gap(.25em);
  }
}

.size{
  display: flex;
}

.size-item{
  margin-right: .41em;
  text-align: center;
  &._disabled{
    color: $textLightGray;
    .cartcalc{
      pointer-events: none;
      button,
      input{
        color: $textLightGray;
      }
    }
  }
  .text12{
    line-height: 200%;
  }
  .cartcalc{
    width: 1.5em;
    height: calc(4.375em + 2px);
    grid-template-columns: 100%;
    grid-template-rows: 1.45em 1.475em 1.45em;
    border-radius: 0;
    margin: .13em 0 0;
    input{
      border: none;
      border-top: 1px solid $dividerGray;
      border-bottom: 1px solid $dividerGray;
    }
  }
}
