.main-slider{
  position: relative;
  overflow: hidden;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      bottom: 3.125em;
    }
  }
  &__img{
    height: 100%;
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.holding{
  padding: 2em 0 3.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4em));
    gap: 1.875em 6em;
    margin-top: 1.875em;
  }
}

.holding-card{
  &:hover{
    color: $black;
  }
  &__img{
    @include imgCover(100%);
  }
  &__content{
    margin-top: 1.125em;
    text-align: center;
    @include imgCover(.625em);
  }
}

.team{
  padding: 3.5em 0 2.56em;
  .info-top{
    padding: 0 1em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.ceo{
  margin-top: 3.125em;
  padding: 6.25em 12em 6.625em;
  background-size: cover;
  background-position: center;
  color: #fff;
  &__desc{
    padding: .19em 0 0 1.625em;
    border-left: 1px solid #fff;
    min-height: 15.4375em;
    margin: 3em 0 0 1.8em;
    .text14{
      line-height: 215%;
    }
  }
}

.team-card{
  border-bottom: 1px solid $dividerGray;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(56.25%);
  }
  &__content{
    flex-grow: 1;
    padding: 1.5em 1.88em 2.62em;
    border-right: 1px solid $dividerGray;
  }
  &__info{
    @include grid100gap(1.875em);
  }
  .text35{
    line-height: 92%;
  }
  &__nav{
    display: flex;
  }
}

.news{
  padding: 2.66em 0 1em;
}

.news-slider{
  .swiper{
    &-container{
      width: calc(100% + 5.625em);
    }
    &-slide{
      width: 25%;
      padding-right: 5.625em;
    }
  }
}

.news-card{
  display: block;
  &:hover{
    color: $black;
  }
  &__img{
    @include imgCover(67%);
  }
  &__content{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
}

.brands{
  padding: 1em 0 2.66em;
  overflow: hidden;
  &__grid{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: .56em;
    margin-left: -1.875em;
    width: calc(100% + 3.75em);
  }
}

.brand-item{
  margin: 1.31em 1.875em 0;
  width: calc(100% / 3 - 3.75em);
  img{
    width: 100%;
    display: block;
  }
}

.reviews,
.technologies,
.honor{
  padding: 2.66em 0;
}

.review-card{
  &__text{
    line-height: 135%;
  }
  &__info{
    margin-top: .625em;
  }
}

.technologies-slider{
  .swiper{
    &-container{
      width: calc(100% + 1.625em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 1.625em;
    }
  }
}

.technology-card{
  background-color: $bgGray;
  height: 100%;
  &__img{
    @include imgCover(59%);
  }
  &__content{
    padding: 1.25em 1.25em 3.25em;
  }
  .text35{
    line-height: 92%;
  }
  &__desc{
    margin-top: 1.8em;
  }
  &__nav{
    display: flex;
    margin-top: .625em;
  }
}

.we-on-youtube{
  padding: 2.66em 0 5.32em;
}

.youtube-slider{
  .swiper{
    &-container{
      width: calc(100% + 2.94em);
    }
    &-slide{
      width: 20%;
      padding-right: 2.94em;
    }
    &-pagination{
      margin-top: .32em;
      position: static;
      &-bullet{
        transform: unset;
      }
    }
  }
}

.youtube-card{
  &__img{
    @include imgCover(56.19%);
  }
  &__content{
    padding: .3125em .625em 0;
  }
  .text18{
    line-height: 135%;
    letter-spacing: unset;
  }
}

.subscribe{
  padding: 2.5em 0;
  background-color: $black;
  color: #fff;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    align-items: center;
  }
  &__badge{
    @include center;
    min-height: 3.125em;
    padding: .2em 1em;
    background-color: $green;
    span{
      letter-spacing: 0.1125em;
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .25em;
    }
    &._red{
      background-color: $primary;
    }
  }
}

.subscribe-info{
  @include grid100gap(.5em);
}

.subscribe-form{
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 10.3em) 10.3em;
  }
  &__field{
    input{
      width: 100%;
      font-size: 1.125em;
      line-height: 120%;
      height: 2.78em;
      border: 1px solid #fff;
      background-color: #fff;
      padding: 0 .55em;
      &:focus{
        border-color: $primary;
      }
      &::placeholder{
        color: $textLightGray;
      }
    }
  }
  &__btn{
    padding: 0;
    width: 100%;
  }
  &__check{
    margin-top: 1.25em;
    label{
      &:before{
        border: none;
        background-color: #fff;
        border-radius: 0;
      }
    }
    span{
      line-height: 143%;
    }
  }
}

.honor{
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(4,25%);
    gap: 1.25em 0;
  }
}

.honor-card{
  text-align: center;
  &__img{
    height: 4.6875em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__hint{
    line-height: 200%;
  }
}
