@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.625em;
    height: 3.125em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 144%;
        letter-spacing: 0.05625em;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    color: $primary;
    &:hover{
        img{
            transform: translateX(.5em);
        }
    }
    img{
        width: 1.5em;
        transition: inherit;
    }
}

.m-btn-default{
    position: relative;
    &._gray:not(:hover){
        &:before,
        &:after{
            background-color: $dividerGray;
        }
    }
    &:before,
    &:after{
        content: "";
        display: block;
        position: absolute;
        top: 0;
        height: 100%;
        width: 12.5em;
        max-width: .875em;
        background-color: $primary;
        z-index: -1;
        -ms-transform: scale(1, 1) skew(-20deg, 0);
        transform: scale(1, 1) skew(-20deg, 0);
        transform: scale3d(1, 1, 1) skewX(-20deg);
        will-change: max-width;
        transition-property: max-width;
        transition-duration: 0.35s;
        transition-timing-function: cubic-bezier(0.68, -0.55, 0.27, 1.55);
    }
    &:hover{
        color: #fff;
        &::before,
        &::after {
            max-width: calc(50% + 2px);
        }
    }

    &::before {
        left: calc(50% - 2px);
    }

    &::after {
        right: calc(50% - 2px);
    }
}

.m-btn-red{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-red-outline{
    border: 1px solid $primary;
    color: $primary;
    padding: 0 1em;
    height: 2.5em;
    font-weight: 700;
    border-radius: .25em;
    span{
        font-size: .875em;
    }
    &:hover{
        background-color: $primary;
        color: #fff;
    }
    &._gray:not(:hover){
        border-color: $dividerGray;
        color: $black;
    }
}

.red-link{
    color: $primary;
    text-decoration: underline;
}

.red-link2{
    color: $primary;
    &:hover{
        color: $black;
    }
}
