.error404{
  padding: 2.88em 0 3.12em;
  &__inner{
    padding-bottom: 1.25em;
    border-bottom: 1px solid #E4E7EC;
  }
  &__img{
    display: block;
    width: 33.625em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__content{
    margin-top: 1.25em;
    text-align: center;
    @include grid100gap(1.875em);
  }
  &__title{
    padding: .625em;
  }
  &__desc{
    color: #333;
    a{
      color: $primary;
      &:hover{
        color: $black;
      }
    }
  }
}
