@import "../vars.scss";
.breadcrumbs {
    padding: .625em 0 1.875em;
    &__item {
        font-size: .875em;
        line-height: 157%;
        position: relative;
        font-weight: 400;
        display: inline;
        letter-spacing: 0.05625em;
        &:not(:first-child) {
            &:before {
                content: ' / ';
                margin: 0 .25em;
                color: $black;
            }
        }
        &:last-child{
            color: $textLightGray;
        }
    }
}
