.search{
  padding: 0 0 3.125em;
  .catalog__items{
    margin-top: 1.25em;
  }
  @media screen and (min-width: 993px){
    .catalog__items{
      grid-template-columns: repeat(4,25%);
    }
    .catalog-item{
      &:nth-child(4){
        border-top: 1px solid $dividerGray;
      }
      &:nth-child(3n - 2){
        border-left: none;
      }
      &:nth-child(4n - 3){
        border-left: 1px solid $dividerGray;
      }
    }
  }
}

.search-top{
  @include grid100gap(.5em);
  padding: 1em 2em 2em;
  background-color: $bgGray;
  .text16{
    line-height: 188%;
    a{
      color: $primary;
      border-bottom: 1px dashed $primary;
      &:hover{
        border-color: transparent;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 10.0625em) 9.0625em;
    gap: 1em;
  }
  .fg{
    input{
      font-size: 2.125em;
      font-weight: 700;
      height: 2.09em;
      border-radius: .23em;
      padding: 0 .5em;
      background-color: #fff;
    }
  }
  &__btn{
    height: 4.4375em;
    font-weight: 600;
    span{
      font-size: 2.125em;
      line-height: 150%;
    }
  }
}

.search-info{
  margin-top: 2.625em;
  @include grid100gap(.625em);
  .text14{
    b{
      font-weight: 700;
    }
  }
}

.search-links{
  display: flex;
  margin-top: .625em;
  &__item{
    border-bottom: 2px solid transparent;
    padding: 0 1.25em;
    height: 3.125em;
    display: flex;
    align-items: center;
    font-weight: 600;
    &._active,
    &:hover{
      color: $black;
      border-color: $primary;
    }
    span{
      font-size: 1em;
      line-height: 189%;
      letter-spacing: 0.05625em;
    }
  }
}
