.cart{
  padding: 0 0 3.125em;
  &__total{
    margin-top: 1em;
    display: flex;
    justify-content: flex-end;
    .text14{
      margin-left: .7em;
    }
  }
  &__bottom{
    margin-top: 4.2em;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__hint{
    margin-top: .625em;
    max-width: 21em;
    text-align: center;
  }
  &__detail{
    margin-top: .625em;
    padding: 0 3.56em 0 1.25em;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__all{
    @include grid100gap(.5em);
  }
  &__prices{
    @include grid100gap(.25em);
  }
  &__price{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    .text14{
      padding-bottom: .125em;
      margin-right: .7em;
    }
    .text16{
      margin-left: .125em;
    }
  }
  &__action{
    margin-top: .625em;
    border-top: 1px solid $dividerGray;
    border-bottom: 1px solid $dividerGray;
    padding: 1em 0;
    @include center;
    &-btn{
      display: flex;
      align-items: center;
      &:hover{
        color: $primary;
        path{
          stroke: $primary;
        }
      }
      &:not(:last-child):after{
        content: '';
        width: .125em;
        height: 1em;
        flex-shrink: 0;
        margin: 0 .8em;
        background-color: #E2E2E2;
      }
      svg{
        width: 1.5em;
        height: auto;
        margin-right: .5em;
        flex-shrink: 0;
      }
      span{
        font-size: .875em;
      }
    }
  }
}

.cart-sales,
.cart-table{
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th{
    background-color: $bgGray;
    height: 2.75em;
    span{
      font-size: .75em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.05625em;
    }
  }
  th,
  td{
    padding-left: 1em;
    text-align: left;
    &:nth-child(1){
      padding-left: 1.25em;
      width: 2.75em;
    }
  }
  .m-check {
    input:checked + label:before {
      background-color: $primary;
      border-color: $primary;
    }

    label {
      padding-left: 1.5em;

      &:before {
        background-color: #fff;
      }

      &:after {
        background-image: url(../img/check5.svg);
        background-size: 1em auto;
      }
    }
  }
}

.cart-table{
  th,
  td{
    &:nth-child(2){
      width: 7.1875em;
    }
    &:nth-child(3){
      width: 4.5em;
    }
    &:nth-child(4){
      width: 4.5em;
    }
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4){
      text-align: center;
    }
    &:nth-child(6){
      width: 9.25em;
    }
    &:last-child{
      width: 4em;
    }
  }
  td{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    border-bottom: 1px solid $dividerGray;
  }
  .m-check{
    input{
      &._halfChecked+label{
        &:before{
          background-color: $primary;
          border-color: $primary;
        }
        &:after{
          background-image: url(../img/minus.svg);
          opacity: 1;
        }
      }
    }
  }
  &__img{
    height: 7.1875em;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__sizes{
    @include grid100gap(.25em);
    @include sameNumWidth;
  }
  .cartcalc{
    width: 8.25em;
    height: 2.75em;
    grid-template-columns: 2.75em 2.75em 2.75em;
    input{
      font-size: 1.125em;
    }
    button{
      font-size: 2.125em;
      line-height: 120%;
    }
  }
  &__delete{
    width: 4em;
    max-width: 100%;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__prices{
    @include grid100gap(.5em);
    white-space: nowrap;
  }
  &__title{
    td{
      padding: .625em 0 0 0 !important;
      border: none;
    }
  }
}

.cart-sales{
  margin-top: 2.125em;
  &__top{
    padding: 0 1.25em;
  }
  table{
    border-spacing: 0 .625em;
    border-collapse: unset;
  }
  th,td{
    &:nth-child(2){
      width: 30.375em;
    }
  }
}

.order{
  margin-top: 1em;
  padding: 1.25em;
  &__subtitle{
    padding: 1.25em 0;
    border-bottom: 1px solid $dividerGray;
    &._no-border{
      border: none;
    }
  }
  &__upload{
    @include grid100gap(.75em);
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
  }
  .m-check{
    label{
      span{
        line-height: 143%;
      }
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
    max-width: 52.625em;
  }
}

.file{
  display: flex;
  align-items: flex-start;
  padding: 1em;
  border-radius: .5em;
  border: 1px solid $dividerGray;
  .upload__icon{
    flex-shrink: 0;
    margin: 0 1em 0 0;
  }
  &__content{
    padding-right: .5em;
  }
  &__delete{
    margin-left: auto;
    width: 1.25em;
    flex-shrink: 0;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.cart-nav{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 1.25em 0;
  box-shadow: 0px -16px 16px 0px rgba(0, 0, 0, 0.10), 0px -8px 8px 0px rgba(0, 0, 0, 0.05);
  background-color: #fff;
  z-index: 20;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__action{
    margin-left: 1.875em;
    min-width: 16.75em;
    .nice-select{
      background-image: url(../img/select-arrow3.svg)!important;
    }
    .list{
      box-shadow: 0 -1em 1em 0 rgba(0,0,0,.1),0 .5em .5em 0 rgba(0,0,0,.05);
      top: unset;
      bottom: 100%;
      margin-bottom: .25em;
    }
  }
  &__btn{
    margin-left: .5em;
  }
}
