.footer{
  padding: 3.56em 0 8.2em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 10.9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__col{
    max-width: 25em;
  }
  &__c{
    display: flex;
    align-items: flex-start;
    margin-top: 1.56em;
    &-icon{
      width: 1.25em;
      height: 1.5em;
      flex-shrink: 0;
      margin-right: .06em;
      @include center;
    }
    img{
      width: .75em;
    }
  }
  .text18{
    line-height: 133%;
  }
  &__address{
    margin-top: 1.625em;
  }
  &__links{
    max-width: 11.8em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.56em;
  }
  &__link{
    @include lastM(0 0 .625em);
  }
}

.footer-contacts{
  margin-top: 1.5em;
  &__items{
    margin-top: .625em;
    @include grid100gap(1.56em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  .social{
    margin-left: .625em;
  }
}

.social{
  display: flex;
  align-items: center;
  &__item{
    width: 1.3125em;
    margin-right: .625em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.scroll-up {
  position: fixed;
  width: 3em;
  height: 3em;
  bottom: 2em;
  right: 3.75em;
  z-index: 20;
  opacity: 0;
  pointer-events: none;
  @include center;
  padding: 0;
  &--active {
    opacity: 1;
    pointer-events: unset;
  }
  svg{
    width: 1.625em;
    height: auto;
  }
}
