.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba($black, .6);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 57.5em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        overflow: hidden;
    }
    &__scroll{
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        padding: 2em 3em;
    }
    &__top{
        padding: 2.5em;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: .625em;
        right: .625em;
        background-color: transparent;
        width: 1.5em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                stroke: $primary;
            }
        }
    }
    &__desc{
        margin-top: 1em;
        text-align: center;
    }
    &__title{
        text-align: center;
    }
    .auth-form{
        margin: 2.25em 0 0;
    }
}

.login-modal{
    .auth-form{
        @include grid100gap(1em);
    }
    .form-items{
        padding: 0 0 1.25em;
        @include grid100gap(1.25em);
        &._no-border{
            border: none;
            padding: 0;
        }
    }
    .form-item{
        &__fields{
            max-width: 100%;
            grid-template-columns: repeat(2,calc(50% - .5em));
        }
    }
}

.client-modal{
    .modal__dialog{
        width: 30.5625em;
    }
    .auth-form__nav{
        margin-top: 1.5em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            overflow: unset;
            overflow-x: hidden;
        }
    }

}


@media screen and (max-width: 576px){
    .modal{

    }
}
