.catalog-item{
  padding-bottom: 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
    gap: 3.125em;
    margin-top: .375em;
  }
  &__content{
    padding: 1.25em;
    border: 1px solid $dividerGray;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    .product-props{
      margin: 0;
      &__item{
        &:last-child{
          margin-right: 0;
        }
      }
    }
    .rating{
      margin-top: .625em;
    }
  }
  &__colors{
    margin-top: .3125em;
    display: flex;
    flex-wrap: wrap;
  }
  &__color{
    width: 3.125em;
    height: 3.125em;
    flex-shrink: 0;
    margin: .3125em .3125em 0 0;
    position: relative;
    border-radius: 0.25em;
    border: 2px solid transparent;
    overflow: hidden;
    display: block;
    transition: all .3s;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
    &:hover,
    &._active{
      border-color: $primary;
    }
  }
  &__desc{
    margin-top: 1.875em;
    max-width: 18.5em;
  }
}

.gallery{
  display: flex;
  justify-content: space-between;
  padding-left: .625em;
  &__small{
    width: 4.375em;
    flex-shrink: 0;
    padding: 1.13em 0 2.8em;
    height: 31.875em;
    position: relative;
    .swiper{
      height: 100%;
      overflow: unset;
      &-slide{
        transition: all .3s;
        width: 100%;
        height: 4.6875em;
        padding-bottom: .3125em;
        opacity: 0;
        pointer-events: none;
        &-visible{
          opacity: 1;
        }
        &-thumb-active{
          .gallery__img{
            border-color: $primary;
          }
        }
      }
    }
    .gallery__img{
      transition: all .3s;
      border: 1px solid #CECECE;
    }
  }
  &__big{
    width: calc(100% - 8.75em);
  }
  .gallery__img{
    display: block;
    @include imgCover(100%);
  }
  .swiper{
    &-button{
      left: 50%;
      transform: translateX(-50%);
      margin: 0;
      width: 1.5em;
      height: 1.5em;
      &-next{
        top: unset;
        bottom: 1.13em;
      }
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.may-like{
  margin-top: 3em;
  &__top{
    padding-left: 1.25em;
  }
  &__slider{
    margin-top: .62em;
    width: 39em;
    padding: 0 2.5em 0 1.25em;
    .swiper{
      &-container{
        overflow: hidden;
        width: calc(100% + 2.5em);
      }
      &-slide{
        width: 25%;
        padding-right: 2.5em;
      }
      &-pagination{
        position: static;
        width: 100%;
        margin-top: 2.25em;
        &-bullet{
          background: #D6D6D6;
          width: .625em;
          height: .625em;
          border-radius: 50%;
          transform: unset;
          border: none;
          &-active{
            background: #869791;
          }
        }
      }
      &-button{
        width: 1.5em;
        height: 1.5em;
        img{
          width: 100%;
          display: block;
        }
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }
}

.like-card{
  &__img{
    position: relative;
    padding-bottom: 130%;
    margin-bottom: 1.25em;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    line-height: 188%;
    b{
      font-weight: 700;
    }
  }
}

.product-info{
  margin-top: .625em;
  &._active{
    .product-info__btn{
      img{
        transform: rotate(-180deg);
      }
    }
  }
  &__items{
    margin-top: .625em;
  }
  &__item{
    line-height: 167%;
    .light-gray-text{
      margin-right: .125em;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__btn{
    width: 1.5em;
    margin-left: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
      transition: all .4s;
    }
  }
  &__hidden{
    margin-top: .625em;
    display: none;
    .product-info__items{
      margin: 0;
    }
  }
  &__text{
    @include grid100gap(1.875em);
    p{
      font-size: .875em;
      line-height: 167%;
      letter-spacing: 0.05625em;
    }
  }
}

.reviews{
  margin-top: 2.5em;
  padding: 0 1.25em;
  max-width: 35.2em;
  &__top{
    @include grid100gap(1em);
    .text20{
      line-height: 150%;
    }
  }
  &__items{
    margin-top: 2em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.25em;
  }
}

.review-item{
  display: grid;
  grid-template-columns: 3.125em calc(100% - 4.125em);
  gap: 1em;
  &__user{
    height: 3.125em;
    @include center;
    color: #fff;
    background-color: $primary;
    border-radius: 50%;
  }
  &__top{
    display: flex;
  }
  &__info{
    margin-right: 1.5em;
    flex-shrink: 0;
  }
  .rating-input{
    &__item{
      width: 1.25em;
      margin-right: .125em;
    }
  }
  &__advantages{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
}

.models{
  padding: 1em;
  margin-top: 1.25em;
  border-radius: .25em;
  border: 1px solid $dividerGray;
  &.models2{
    padding: 0;
    border: none;
    border-radius: 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__col{
    padding: 1em;
    border-radius: .25em;
    border: 1px solid $dividerGray;
    display: flex;
    flex-direction: column;
  }
  &__items{
    margin-bottom: 1em;
    @include grid100gap(1em);
    &._gap8{
      gap: .5em;
    }
  }
  &__item{
    max-width: 13.9375em;
    &-bottom{
      display: grid;
      grid-template-columns: calc(100% - 4.875em) 4.375em;
      gap: .5em;
    }
    .text14{
      line-height: 172%;
    }
    .text12{
      letter-spacing: unset;
      line-height: 200%;
    }
    &-grid{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: .5em 1.5em;
      margin-top: .5em;
      max-width: 13em;
    }
    &-col{
      display: grid;
      grid-template-columns: 1.4em calc(100% - 1.4em);
      align-items: center;
      @include sameNumWidth;
      .cartcalc{
        margin: 0;
      }
    }
  }
  &__bottom,
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-right: 1.5em;
    white-space: nowrap;
  }
  &__bottom{
    margin-top: 1.625em;
    .text14{
      line-height: 143%;
    }
  }
}

.models2{
  .models__items{
    margin: 1.625em 0 0;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2.125em;
    display: flex;
    justify-content: space-between;
    .text14{
      line-height: 172%;
    }
  }
  &__nav{
    display: flex;
  }
}
